body {
  background-color: #f0f2f5;
}
#root {
  background-image: url(/assets/svg/background.svg);
  background-repeat: no-repeat;
  background-position: center 110px;
  -webkit-background-size: 100% 100%;
     -moz-background-size: 100%;
          background-size: 100%;
}
#root .app-loadable-loading {
  height: 100vh;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
#root .app-loadable-loading > div {
  width: 200px;
  height: 200px;
  background-image: url(/assets/svg/loading.svg);
  background-repeat: no-repeat;
  background-position: center ;
  -webkit-background-size: 100% 100%;
     -moz-background-size: 100%;
          background-size: 100%;
}
::-webkit-scrollbar-track-piece {
  width: 6px;
  background-color: #bfbfbf;
}
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  cursor: pointer;
}
::-webkit-scrollbar-thumb:hover {
  background: #7b7b7b;
  cursor: pointer;
}
/* 过度动画 */
.fade-enter {
  opacity: 0;
  -webkit-animation-duration: 0.2s;
     -moz-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-fill-mode: both;
     -moz-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
     -moz-animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
          animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
  -webkit-animation-play-state: paused;
     -moz-animation-play-state: paused;
          animation-play-state: paused;
}
.fade-appear {
  opacity: 0;
  -webkit-animation-duration: 0.2s;
     -moz-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-fill-mode: both;
     -moz-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
     -moz-animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
          animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
  -webkit-animation-play-state: paused;
     -moz-animation-play-state: paused;
          animation-play-state: paused;
}
.fade-leave {
  -webkit-animation-duration: 0.2s;
     -moz-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-fill-mode: both;
     -moz-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
     -moz-animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
          animation-timing-function: cubic-bezier(0.55, 0, 0.55, 0.2);
  -webkit-animation-play-state: paused;
     -moz-animation-play-state: paused;
          animation-play-state: paused;
}
.fade-enter.fade-enter-active {
  -webkit-animation-name: fadeIn;
     -moz-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-animation-play-state: running;
     -moz-animation-play-state: running;
          animation-play-state: running;
}
.fade-appear.fade-appear-active {
  -webkit-animation-name: fadeIn;
     -moz-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-animation-play-state: running;
     -moz-animation-play-state: running;
          animation-play-state: running;
}
.fade-leave.fade-leave-active {
  -webkit-animation-name: fadeOut;
     -moz-animation-name: fadeOut;
          animation-name: fadeOut;
  -webkit-animation-play-state: running;
     -moz-animation-play-state: running;
          animation-play-state: running;
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@-moz-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.ant-table .react-resizable {
  position: relative;
}
.ant-table .react-resizable-handle {
  position: absolute;
  width: 10px;
  height: 100%;
  bottom: 0;
  right: -5px;
  cursor: col-resize;
}
